import { useState, useEffect } from "react";
import "@/App.css";
import axios from "axios";
import { LineChart, Line, XAxis, YAxis, CartesianGrid, Tooltip, Legend, ResponsiveContainer, Area, AreaChart } from 'recharts';
import { Activity, TrendingUp, Server, Clock, Zap, Calendar, CalendarDays } from 'lucide-react';
import { Button } from '@/components/ui/button';
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select';
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card';
import { Badge } from '@/components/ui/badge';
import { Toaster, toast } from 'sonner';
import { Popover, PopoverContent, PopoverTrigger } from '@/components/ui/popover';
import { Calendar as CalendarComponent } from '@/components/ui/calendar';
import { Tabs, TabsContent, TabsList, TabsTrigger } from '@/components/ui/tabs';
import { format } from 'date-fns';
import FestivalCalendar from '@/components/FestivalCalendar';

const BACKEND_URL = process.env.REACT_APP_BACKEND_URL || 'http://localhost:8000';
const API = `${BACKEND_URL}/api`;

function App() {
  const [predictions, setPredictions] = useState([]);
  const [selectedModel, setSelectedModel] = useState('catboost');
  const [activeTab, setActiveTab] = useState('predictions');
  const [loading, setLoading] = useState(false);
  const [scalingResult, setScalingResult] = useState(null);
  const [reasonLoading, setReasonLoading] = useState(false);
  const [reasonText, setReasonText] = useState('');
  const [selectedDate, setSelectedDate] = useState(new Date());
  const [calendarOpen, setCalendarOpen] = useState(false);
  const [nextFestival, setNextFestival] = useState(null);
  const [currentMetrics, setCurrentMetrics] = useState({
    avgLoad: 0,
    peakLoad: 0,
    recommendedInstances: 0,
    festivals: 0
  });

  // Fetch next upcoming festival
  const fetchNextFestival = async (modelName) => {
    try {
      const response = await axios.get(`${API}/next-festival`, {
        params: { model_name: modelName }
      });
      
      if (response.data.festival_name) {
        setNextFestival(response.data);
      } else {
        setNextFestival(null);
      }
    } catch (error) {
      console.error('Error fetching next festival:', error);
    }
  };

  // Fetch 24-hour predictions
  const fetchPredictions = async (modelName, startDate = null) => {
    setLoading(true);
    try {
      const predictDate = startDate || selectedDate;
      const response = await axios.post(`${API}/predict`, {
        start_time: predictDate.toISOString(),
        hours: 24,
        model_name: modelName
      }, {
        timeout: 60000 // 60 second timeout for model predictions
      });
      
      const data = response.data;
      setPredictions(data);
      
      // Calculate metrics
      const loads = data.map(p => p.predicted_load);
      const avgLoad = loads.reduce((a, b) => a + b, 0) / loads.length;
      const peakLoad = Math.max(...loads);
      const festivals = data.filter(p => p.is_festival === 1).length;
      
      let recommendedInstances = 2;
      if (peakLoad > 5000) recommendedInstances = 10;
      else if (peakLoad > 3000) recommendedInstances = 5;
      else if (peakLoad > 1500) recommendedInstances = 3;
      
      setCurrentMetrics({
        avgLoad: Math.round(avgLoad),
        peakLoad: Math.round(peakLoad),
        recommendedInstances,
        festivals
      });
      
      toast.success(`Predictions loaded for ${format(predictDate, 'MMM dd, yyyy')} with ${modelName.toUpperCase()}`);
    } catch (error) {
      console.error('Error fetching predictions:', error);
      const errorMessage = error.response?.data?.detail || error.message || 'Failed to fetch predictions';
      toast.error(`Failed to fetch predictions: ${errorMessage}`);
      console.error('Full error:', error);
      console.error('Backend URL:', API);
    } finally {
      setLoading(false);
    }
  };

  // Trigger AWS scaling
  const triggerScaling = async () => {
    if (predictions.length === 0) {
      toast.error('Generate predictions first');
      return;
    }
    
    try {
      const peakPrediction = Math.max(...predictions.map(p => p.predicted_load));
      const response = await axios.post(`${API}/scale`, {
        predicted_load: peakPrediction,
        asg_name: 'my-web-asg'
      });
      
      setScalingResult(response.data);
      
      if (response.data.mode === 'mock') {
        toast.info('Mock scaling (Add AWS credentials for real scaling)');
      } else {
        toast.success('AWS Auto Scaling triggered!');
      }
    } catch (error) {
      console.error('Error triggering scaling:', error);
      toast.error('Failed to trigger scaling');
    }
  };

  // Request explanation / reasoning from backend (Gemini proxy or local fallback)
  const requestReasoning = async () => {
    if (!currentMetrics || !currentMetrics.peakLoad) {
      toast.error('No metrics available to explain');
      return;
    }

    setReasonLoading(true);
    setReasonText('');
    try {
      const payload = {
        context: {
          peakLoad: currentMetrics.peakLoad,
          recommendedInstances: currentMetrics.recommendedInstances,
          model: selectedModel,
          date: selectedDate.toISOString()
        }
      };

      const resp = await axios.post(`${API}/reason`, payload, { timeout: 20000 });
      const data = resp.data;

      if (data.mode === 'remote') {
        // Try to extract a human-readable message from provider response
        if (data.provider_response && typeof data.provider_response === 'object') {
          // Heuristics: some providers return {candidates: [{content:{parts:[{text}]}}]}
          try {
            const cand = data.provider_response.candidates?.[0];
            const text = cand?.content?.parts?.[0]?.text;
            if (text) {
              setReasonText(text);
            } else {
              setReasonText(JSON.stringify(data.provider_response));
            }
          } catch (e) {
            setReasonText(JSON.stringify(data.provider_response));
          }
        } else {
          setReasonText(String(data.provider_response || 'No remote response'));
        }
      } else if (data.mode === 'local') {
        setReasonText(data.explanation || 'No explanation returned');
      } else {
        setReasonText(JSON.stringify(data));
      }

    } catch (error) {
      console.error('Error requesting reasoning:', error);
      toast.error('Failed to get explanation');
    } finally {
      setReasonLoading(false);
    }
  };

  // Handle model change
  const handleModelChange = (model) => {
    setSelectedModel(model);
    fetchPredictions(model, selectedDate);
    fetchNextFestival(model);
  };

  // Handle date change
  const handleDateChange = (date) => {
    if (date) {
      setSelectedDate(date);
      setCalendarOpen(false);
      fetchPredictions(selectedModel, date);
    }
  };

  // Load predictions on mount
  useEffect(() => {
    fetchPredictions('catboost', new Date());
    fetchNextFestival('catboost');
  }, []);

  // Prepare chart data
  const chartData = predictions.map((pred, idx) => ({
    hour: `${pred.hour}:00`,
    load: Math.round(pred.predicted_load),
    isFestival: pred.is_festival === 1,
    festivalName: pred.festival_name
  }));

  return (
    <div className="app-container" data-testid="app-container">
      <Toaster position="top-right" richColors />
      
      {/* Header */}
      <header className="app-header" data-testid="app-header">
        <div className="header-content">
          <div className="header-left">
            <div className="logo-container">
              <Zap className="logo-icon" />
              <h1 className="app-title">AI Predictive Autoscaling</h1>
            </div>
            <p className="app-subtitle">Real-time traffic prediction with intelligent EC2 scaling</p>
          </div>
          
          <div className="header-right">
            <div className="date-picker-container">
              <label className="date-label">Predict Date:</label>
              <Popover open={calendarOpen} onOpenChange={setCalendarOpen}>
                <PopoverTrigger asChild>
                  <Button
                    variant="outline"
                    className="date-picker-button"
                    data-testid="date-picker-button"
                  >
                    <CalendarDays className="calendar-icon-btn" />
                    {format(selectedDate, 'MMM dd, yyyy')}
                  </Button>
                </PopoverTrigger>
                <PopoverContent className="calendar-popover" align="start">
                  <CalendarComponent
                    mode="single"
                    selected={selectedDate}
                    onSelect={handleDateChange}
                    initialFocus
                    fromDate={new Date(2024, 0, 1)}
                    toDate={new Date(2026, 11, 31)}
                    data-testid="calendar-component"
                  />
                </PopoverContent>
              </Popover>
            </div>
            
            <div className="model-selector">
              <label className="model-label">ML Model:</label>
              <Select value={selectedModel} onValueChange={handleModelChange}>
                <SelectTrigger className="model-trigger" data-testid="model-selector">
                  <SelectValue />
                </SelectTrigger>
                <SelectContent>
                  <SelectItem value="catboost">CatBoost</SelectItem>
                  <SelectItem value="lightgbm">LightGBM</SelectItem>
                  <SelectItem value="xgboost">XGBoost</SelectItem>
                </SelectContent>
              </Select>
            </div>
          </div>
        </div>
      </header>

      {/* Main Content */}
      <main className="main-content">
        {/* Tabs for different views */}
        <Tabs value={activeTab} onValueChange={setActiveTab} className="mb-6">
          <TabsList className="grid w-full max-w-md grid-cols-2">
            <TabsTrigger value="predictions">24-Hour Predictions</TabsTrigger>
            <TabsTrigger value="festivals">Festival Calendar</TabsTrigger>
          </TabsList>

          {/* Tab: 24-Hour Predictions */}
          <TabsContent value="predictions" className="space-y-6">
        {/* Next Festival Alert */}
        {nextFestival && nextFestival.festival_name && (
          <Card className="next-festival-card" data-testid="next-festival-card">
            <CardContent className="festival-alert-content">
              <div className="festival-alert-icon">
                <Calendar className="alert-icon-large" />
              </div>
              <div className="festival-alert-details">
                <div className="festival-alert-header">
                  <h3 className="festival-alert-title"> Upcoming Festival Alert</h3>
                  <Badge className="days-badge">{nextFestival.days_until} days away</Badge>
                </div>
                <p className="festival-name-large">{nextFestival.festival_name}</p>
                <p className="festival-date">{format(new Date(nextFestival.date), 'EEEE, MMMM dd, yyyy')}</p>
                
                <div className="festival-metrics">
                  <div className="festival-metric-item">
                    <Activity className="festival-metric-icon" />
                    <div>
                      <div className="festival-metric-label">Avg Traffic</div>
                      <div className="festival-metric-value">{nextFestival.avg_load}</div>
                    </div>
                  </div>
                  
                  <div className="festival-metric-item">
                    <TrendingUp className="festival-metric-icon peak" />
                    <div>
                      <div className="festival-metric-label">Peak Load</div>
                      <div className="festival-metric-value peak">{nextFestival.peak_load}</div>
                    </div>
                  </div>
                  
                  <div className="festival-metric-item">
                    <Server className="festival-metric-icon instances" />
                    <div>
                      <div className="festival-metric-label">Recommended</div>
                      <div className="festival-metric-value instances">{nextFestival.recommended_instances} instances</div>
                    </div>
                  </div>
                </div>
                
                <Button 
                  onClick={() => handleDateChange(new Date(nextFestival.date))}
                  className="view-festival-btn"
                  data-testid="view-festival-btn"
                >
                  <CalendarDays className="btn-icon" />
                  View Festival Predictions
                </Button>
              </div>
            </CardContent>
          </Card>
        )}

        {/* Metrics Cards */}
        <div className="metrics-grid" data-testid="metrics-grid">
          <Card className="metric-card">
            <CardHeader className="metric-header">
              <Activity className="metric-icon" style={{ color: '#3b82f6' }} />
              <CardTitle className="metric-title">Avg Load</CardTitle>
            </CardHeader>
            <CardContent>
              <div className="metric-value">{currentMetrics.avgLoad}</div>
              <div className="metric-label">requests/hour</div>
            </CardContent>
          </Card>

          <Card className="metric-card">
            <CardHeader className="metric-header">
              <TrendingUp className="metric-icon" style={{ color: '#ef4444' }} />
              <CardTitle className="metric-title">Peak Load</CardTitle>
            </CardHeader>
            <CardContent>
              <div className="metric-value">{currentMetrics.peakLoad}</div>
              <div className="metric-label">requests/hour</div>
            </CardContent>
          </Card>

          <Card className="metric-card">
            <CardHeader className="metric-header">
              <Server className="metric-icon" style={{ color: '#10b981' }} />
              <CardTitle className="metric-title">Recommended</CardTitle>
            </CardHeader>
            <CardContent>
              <div className="metric-value">{currentMetrics.recommendedInstances}</div>
              <div className="metric-label">EC2 instances</div>
            </CardContent>
          </Card>

          <Card className="metric-card">
            <CardHeader className="metric-header">
              <Calendar className="metric-icon" style={{ color: '#f59e0b' }} />
              <CardTitle className="metric-title">Festivals</CardTitle>
            </CardHeader>
            <CardContent>
              <div className="metric-value">{currentMetrics.festivals}</div>
              <div className="metric-label">in next 24h</div>
            </CardContent>
          </Card>
        </div>

        {/* Prediction Chart */}
        <Card className="chart-card" data-testid="prediction-chart">
          <CardHeader>
            <CardTitle className="chart-title">
              <Clock className="chart-icon" />
              24-Hour Traffic Prediction
            </CardTitle>
            <CardDescription>Hourly traffic forecast for optimal autoscaling</CardDescription>
          </CardHeader>
          <CardContent>
            {loading ? (
              <div className="loading-state">
                <div className="spinner"></div>
                <p>Loading predictions...</p>
              </div>
            ) : (
              <ResponsiveContainer width="100%" height={400}>
                <AreaChart data={chartData}>
                  <defs>
                    <linearGradient id="loadGradient" x1="0" y1="0" x2="0" y2="1">
                      <stop offset="5%" stopColor="#3b82f6" stopOpacity={0.3}/>
                      <stop offset="95%" stopColor="#3b82f6" stopOpacity={0}/>
                    </linearGradient>
                  </defs>
                  <CartesianGrid strokeDasharray="3 3" stroke="#e5e7eb" />
                  <XAxis 
                    dataKey="hour" 
                    stroke="#6b7280"
                    tick={{ fontSize: 12 }}
                  />
                  <YAxis 
                    stroke="#6b7280"
                    tick={{ fontSize: 12 }}
                    label={{ value: 'Traffic Load', angle: -90, position: 'insideLeft', style: { fontSize: 12, fill: '#6b7280' } }}
                  />
                  <Tooltip 
                    contentStyle={{ 
                      backgroundColor: '#fff', 
                      border: '1px solid #e5e7eb',
                      borderRadius: '8px',
                      boxShadow: '0 4px 6px -1px rgb(0 0 0 / 0.1)'
                    }}
                    content={({ active, payload }) => {
                      if (active && payload && payload[0]) {
                        const data = payload[0].payload;
                        return (
                          <div className="custom-tooltip">
                            <p className="tooltip-hour">{data.hour}</p>
                            <p className="tooltip-load">Load: <strong>{data.load}</strong></p>
                            {data.isFestival && (
                              <Badge className="festival-badge">{data.festivalName}</Badge>
                            )}
                          </div>
                        );
                      }
                      return null;
                    }}
                  />
                  <Area 
                    type="monotone" 
                    dataKey="load" 
                    stroke="#3b82f6" 
                    strokeWidth={3}
                    fill="url(#loadGradient)"
                  />
                </AreaChart>
              </ResponsiveContainer>
            )}
          </CardContent>
        </Card>

        {/* Actions & Scaling */}
        <div className="actions-section">
          <Card className="action-card">
            <CardHeader>
              <CardTitle className="action-title">AWS Auto Scaling Control</CardTitle>
              <CardDescription>Trigger EC2 instance scaling based on predictions</CardDescription>
            </CardHeader>
            <CardContent>
              <Button 
                onClick={triggerScaling}
                disabled={loading || predictions.length === 0}
                className="scale-button"
                data-testid="trigger-scaling-btn"
              >
                <Server className="button-icon" />
                Trigger AWS Scaling
              </Button>
              <Button
                onClick={requestReasoning}
                disabled={reasonLoading || !currentMetrics || currentMetrics.peakLoad === 0}
                variant="outline"
                className="explain-button"
                data-testid="explain-recommendation-btn"
                style={{ marginLeft: '0.5rem' }}
              >
                {reasonLoading ? 'Thinking...' : 'Explain Recommendation'}
              </Button>
              
              {scalingResult && (
                <div className="scaling-result" data-testid="scaling-result">
                  <div className="result-header">
                    {scalingResult.mode === 'mock' ? (
                      <Badge variant="outline" className="mode-badge mode-mock">Mock Mode</Badge>
                    ) : (
                      <Badge variant="default" className="mode-badge mode-real">Real AWS</Badge>
                    )}
                  </div>
                  <div className="result-content">
                    <div className="result-item">
                      <span className="result-label">Predicted Load:</span>
                      <span className="result-value">{Math.round(scalingResult.predicted_load)}</span>
                    </div>
                    <div className="result-item">
                      <span className="result-label">Desired Capacity:</span>
                      <span className="result-value">{scalingResult.desired_capacity} instances</span>
                    </div>
                    <p className="result-message">{scalingResult.message}</p>
                  </div>
                </div>
              )}

              {/* Explanation / Reasoning */}
              {reasonText && (
                <div className="reasoning-block" data-testid="reasoning-block" style={{ marginTop: '1rem' }}>
                  <Card>
                    <CardHeader>
                      <CardTitle>Explanation</CardTitle>
                      <CardDescription>Why the autoscaler recommended this capacity</CardDescription>
                    </CardHeader>
                    <CardContent>
                      <pre style={{ whiteSpace: 'pre-wrap', margin: 0 }}>{reasonText}</pre>
                    </CardContent>
                  </Card>
                </div>
              )}
            </CardContent>
          </Card>

          <Card className="info-card">
            <CardHeader>
              <CardTitle className="info-title">Configuration</CardTitle>
            </CardHeader>
            <CardContent>
              <div className="info-list">
                <div className="info-item">
                  <span className="info-key">Selected Date:</span>
                  <span className="info-value">{format(selectedDate, 'MMM dd, yyyy')}</span>
                </div>
                <div className="info-item">
                  <span className="info-key">Model:</span>
                  <span className="info-value">{selectedModel.toUpperCase()}</span>
                </div>
                <div className="info-item">
                  <span className="info-key">Prediction Window:</span>
                  <span className="info-value">24 hours</span>
                </div>
                <div className="info-item">
                  <span className="info-key">Festival API:</span>
                  <span className="info-value">Calendarific</span>
                </div>
                <div className="info-item">
                  <span className="info-key">AWS Service:</span>
                  <span className="info-value">EC2 Auto Scaling</span>
                </div>
              </div>
            </CardContent>
          </Card>
        </div>

        {/* Festival Timeline */}
        {predictions.filter(p => p.is_festival === 1).length > 0 && (
          <Card className="festival-card" data-testid="festival-timeline">
            <CardHeader>
              <CardTitle className="festival-title">
                <Calendar className="festival-icon" />
                Upcoming Festivals
              </CardTitle>
            </CardHeader>
            <CardContent>
              <div className="festival-list">
                {predictions
                  .filter(p => p.is_festival === 1)
                  .map((pred, idx) => (
                    <div key={idx} className="festival-item">
                      <Badge className="festival-time-badge">{pred.hour}:00</Badge>
                      <span className="festival-name">{pred.festival_name}</span>
                      <span className="festival-load">Expected: {Math.round(pred.predicted_load)} req/h</span>
                    </div>
                  ))
                }
              </div>
            </CardContent>
          </Card>
        )}
          </TabsContent>

          {/* Tab: Festival Calendar */}
          <TabsContent value="festivals">
            <FestivalCalendar selectedModel={selectedModel} />
          </TabsContent>
        </Tabs>
      </main>
    </div>
  );
}

export default App;
